.burger {
  display: none;
}

@include high-tablet {
  .burger {
    display: block;
    position: relative;
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 10px;
    width: 44px;
    height: 44px;

    &__line {
      position: absolute;
      top: 50%;
      left: 50%;
      border-radius: 100px;
      width: 26px;
      height: 2px;
      background-color: var(--color-light);
      transform: translateX(-50%) translateY(-50%);

      &:first-child {
        margin-top: -7px;
      }

      &:last-child {
        left: 8px;
        margin-top: 6px;
        width: 13px;
        transform: translateY(50%);
      }
    }
  }

  .burger-close {
    // display: none;
    position: absolute;
    right: var(--container-offset);
    top: 30px;
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 10px;
    width: 44px;
    height: 44px;
    background-image: url("../img/close.svg");
    background-position: center;
    background-size: 14px 12px;
    background-repeat: no-repeat;
  }
}
